bezier(0.4, 0, 0.2, 1);\n    -webkit-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.night .bg-color-1 {\n    background-color: #2d333e;\n    transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -moz-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -webkit-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.night .bg-color-2 {\n    background-color: #191c21;\n    transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -moz-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -webkit-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.day .btn-color-green {\n    color: #f7f7f7;\n    background-color: #1ABC9C;\n    box-shadow: 0px 3px 0px 0px rgb(12, 131, 101);\n}\n\n.day .btn-color-green:active {\n    margin-top: 2px;\n    background-color: #0f7561;\n    box-shadow: 0px 1px 0px 0px rgb(12, 131, 101);\n}\n\n.day .btn-color-green:disabled {\n    color: #d8d8d8;\n    background-color: #bbbbbb;\n    box-shadow: 0px 3px 0px 0px rgb(146 146 146);\n}\n\n.night .btn-color-green {\n    color: white;\n    background-color: #368B85;\n    box-shadow: 0px 3px 0px 0px #173b39;\n}\n\n.night .btn-color-green:active {\n    margin-top: 2px;\n    background-color: #26635e;\n    box-shadow: 0px 1px 0px 0px #26635e;\n}\n\n.night .btn-color-green:disabled {\n    color: #b1b1b1;\n    background-color: #759493;\n    box-shadow: 0px 3px 0px 0px #465a58;\n}\n\n.day .function-bar-mask {\n    transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -o-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -ms-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -moz-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -webkit-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    background-color: rgb(230 230 230 / 80%);\n}\n\n.night .function-bar-mask {\n    transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -o-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -ms-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -moz-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    -webkit-transition: background-color 1.5s cubic-bezier(0.4, 0, 0.2, 1);\n    color: white;\n    background-color: rgb(25 28 33 / 80%);\n}\n\n.color-deep-gray {\n    color: #424949\n}\n\n.color-clould {\n    color: #ecf0f1;\n}\n\n.btn-color-gold {\n    color: white;\n    background: linear-gradient(-60deg, #FFE998, #bb7820, #FFE998, #bd7516);\n    box-shadow: 0px 3px 0px 0px #db8f2b;\n    background-size: 400% 400%;\n    animation: gradient 10s ease infinite !important;\n    height: 100vh;\n}\n\n@keyframes gradient {\n    0% {\n        background-position: 0% 50%;\n    }\n    50% {\n        background-position: 100% 50%;\n    }\n    100% {\n        background-position: 0% 50%;\n    }\n}\n\n.tune_svg_width {\n    width: 217px !important;\n}\n\n.lock-mobile-width {\n    max-width: 540px !important;\n}\n\n.ht-10 {\n    height: 10px;\n}\n\n.ht-20 {\n    height: 20px;\n}\n\n.ht-30 {\n    height: 30px;\n}\n\n.ht-40 {\n    height: 40px;\n}\n\n.ht-50 {\n    height: 50px;\n}\n\n.ht-60 {\n    height: 60px;\n}\n\n.fs-7 {\n    font-size: 0.8rem !important;\n}\n\n\n/* header */\n\n\n/* fixed to center */\n\n.main-menu-btn,\n.header-bar,\n.function-bar,\n.bottom-bar {\n    position: fixed;\n    left: 0;\n    right: 0;\n    margin: 0 auto;\n}\n\n.main-menu-btn {\n    top: 0;\n    height: 50px;\n    z-index: 2000;\n    width: 100%;\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    align-content: center;\n}\n\n.main-menu-btn>div {\n    margin-right: 10px;\n    color: #424242;\n    width: 30px;\n    height: 30px;\n    background-color: #ffffffe5;\n    border-radius: 25px;\n    box-shadow: 0px 1.5px 0.5px 0px rgb(146 145 145 / 90%);\n}\n\n.header-bar {\n    display: block;\n    top: 0px;\n    width: 100%;\n    height: 145px;\n    border-bottom: 0.1em dashed #9e9e9e;\n    z-index: 1000;\n}\n\n.slide-fade-enter-active {\n    transition: all 0.2s ease;\n    -o-transition: all 0.2s ease;\n    -ms-transition: all 0.2s ease;\n    -moz-transition: all 0.2s ease;\n    -webkit-transition: all 0.2s ease;\n}\n\n.slide-fade-leave-active {\n    transition: all 0s ease;\n    -o-transition: all 0s ease;\n    -ms-transition: all 0s ease;\n    -moz-transition: all 0s ease;\n    -webkit-transition: all 0s ease;\n}\n\n.slide-fade-enter,\n.slide-fade-leave-to {\n    opacity: 0;\n}\n\n.host-message {\n    position: relative;\n    display: flex;\n    vertical-align: middle;\n    align-content: center;\n    justify-content: center;\n    flex-direction: column;\n    font-size: 1.5rem;\n    height: 80px;\n    margin: 10px 10px 0px 10px;\n    border-radius: 30px;\n    background-color: #ffffffd4;\n}\n\n.host-text-arrow {\n    position: absolute;\n    bottom: -15px;\n    left: 40px;\n    width: 0;\n    height: 0;\n    border-left: 0px solid transparent;\n    border-right: 15px solid transparent;\n    border-top: 15px solid #ffffffd4;\n}\n\n.judge-icon {\n    position: absolute;\n    top: 90px;\n    width: 50px;\n    height: 50px;\n    margin-left: 10px;\n    color: white;\n    text-shadow: 1px 3px 1px #00000094;\n    font-size: 2.5rem;\n}\n\n.fade-in {\n    animation: fadein 0.2s 1;\n}\n\n@keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n@-moz-keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n@-webkit-keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n.host-tips {\n    display: inline-flex;\n    vertical-align: middle;\n    align-content: center;\n    justify-content: center;\n    flex-direction: column;\n    margin-top: 20px;\n    font-size: 1rem;\n    background-color: #242424c4;\n    color: white;\n    border-radius: 5px;\n    animation: b